@import '~node_modules/modern-normalize/modern-normalize.css';


html {
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

p {
    margin: 0;
}


img {
    display: block;

    height: auto;
}

a {
    text-decoration: none;
    transition: color var(--duration) var(--timing-function);
}

ul,
ol {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin-top: 0;
    margin-bottom: 0;
}
