.movie-card {
  padding: 40px 12px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: auto;
  @media screen and (max-width: 768px) {
    padding-left: 36px;
    padding-right: 72px;
  }
  @media screen and (max-width: 690px) {
    padding-right: 36px;
  }
  @media screen and (max-width: 650px) {
    width: 280px;
    flex-direction: column;
    padding-top: 50px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 40px;
    border: none;
  }
  @media screen and (max-width: 480px) {
  }
  &__poster {
    width: 375px;
    height: 478px;
    border-radius: 5px;
    overflow: hidden;
    & img {
      object-fit: cover;
      object-position: 50% 50%;

      display: block;
      width: 100%;
      height: 100%;
    }
    @media screen and (max-width: 850px) {
      width: 320px;
      height: 410px;
    }
    @media screen and (max-width: 768px) {
      width: 264px;
      height: 373px;
    }
    @media screen and (max-width: 650px) {
      width: 240px;
      height: 357px;
      margin-bottom: 20px;
    }
  }
  &__no-image {
    width: 100%;
  }
  &__info {
    width: 391px;
    height: auto;
    @media screen and (max-width: 850px) {
      width: 340px;
    }
    @media screen and (max-width: 768px) {
      width: 270px;
    }
    @media screen and (max-width: 650px) {
      width: 244px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    color: #000000;
    text-transform: uppercase;
    margin: 0 0 20px;
    @media screen and (max-width: 768px) {
      font-weight: 500;
      font-size: 20px;
      line-height: 23px;
    }
  }

  &__categories {
    display: grid;
    color: #8c8c8c;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 20px;
  }
  &__categories-item {
    display: grid;
    grid-template-columns: 150px 1fr;
    margin: 0 0 10px;
    & p:not(:first-child) {
      color: #000000;
    }
    @media screen and (max-width: 850px) {
      grid-template-columns: 110px 1fr;
    }
  }
  &__votes {
    & span:not(:last-child) {
      margin-right: 7.5px;
    }
    & span:first-child {
      background-color: #ff6b01;
      color: #ffffff;
      border-radius: 5px;
      padding: 1px 5px;
    }
    & span:last-child {
      background-color: #f7f7f7;
      color: #000000;
      border-radius: 5px;
      padding: 1px 5px;
    }
  }
  &__origin-title {
    text-transform: uppercase;
  }
  &__descript {
    color: #000000;
    margin-bottom: 20px;
    width: 390px;
    @media screen and (max-width: 850px) {
      width: 264px;
    }
    @media screen and (max-width: 650px) {
      width: 240px;
    }
  }
  &__descript-title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    margin: 0 0 8px;
    overflow: hidden;
  }
  &__text {
    text-justify: auto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
  }
  &__controls {
    display: flex;
    justify-content: space-between;
    width: 288px;
    @media screen and (max-width: 850px) {
      width: 264px;
    }
    @media screen and (max-width: 650px) {
      width: 240px;
    }
  }
  &__btn {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    border-radius: 5px;
    padding: 0 14px;
    width: 136px;
    height: 44px;
    background: inherit;
    border: 1px solid #000000;
    color: #000000;

    &--active {
      border: 1px solid #ff6b01;
      background-color: #ff6b01;
      color: white;
    }

    &:hover {
      border: 1px solid #ff6b01;
    }
    @media screen and (max-width: 850px) {
      width: 125px;
    }
    @media screen and (max-width: 650px) {
      width: 110px;
      padding: 0 4px;
    }
  }
}
