.backdrop {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms, visibility 250ms;
}

.modal {
  position: relative;
  width: 500px;
  height: 700px;
  background: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  // margin: 0 auto;
  // padding: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow-y: auto;
}

.modal__header {
  width: auto;
}

.modal__close {
  position: absolute;
  width: 25px;
  height: 25px;
  right: 10px;
  top: 10px;
  margin-right: 10px;

  &:hover,
  &:focus {
    border: 1px solid #ff6b08;
    border-radius: 50%;

    // fill: #ffffff;
    cursor: pointer;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-img {
  min-width: 300px;
  min-height: 300px;
}

.team__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.team__card {
  width: 300px;
  height: 400px;
  margin-bottom: 15px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}

.team__info {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  padding-top: 7px;
  padding-left: 10px;
}

.modal__h1 {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.2;
  color: #ff6b08;
  text-align: center;
  padding-bottom: 10px;
  padding-top: 10px;
}

@media (min-width: 768px) {
    .modal {
        width: 768px;
        height: 800px;
    }

  .team__card {
    width: 300px;
    height: 400px;
    &:not(:nth-child(2n + 2)) {
      margin-right: 30px;
    }
    &:nth-child(-n + 8) {
      margin-bottom: 30px;
    }
  }
  .team__info {
      font-size: 20px;
  }
}

@media (min-width: 1280px) {
    .modal {
            width: 1280px;
            height: 800px;
        }
  .team__card {
    width: 300px;
    height: 400px;
    display: flex;
    flex-wrap: wrap;
    &:not(:nth-child(2n + 2)) {
      margin-right: 0;
    }
    &:not(:nth-child(3n + 3)) {
      margin-right: 30px;
    }
    &:nth-child(-n + 8) {
      margin-bottom: 0;
    }
    &:nth-child(-n + 6) {
      margin-bottom: 30px;
    }
  }
  .team__info {
    font-size: 25px;
  }
}

